import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common'; 
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { DetailImportSupplierComponent } from './detail-import-supplier/detail-import-supplier.component';
import { PurchaseComponent } from './purchase.component';
import { BillType2Module } from 'src/app/cafe/components/bill-type/bill-type-2/bill-type2.module';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';
const routes: Routes = [
  {
    path: "",
    component: PurchaseComponent
  },
  {
    path: "add",
    component:AddComponent
  },
  {
    path: "detail",
    component: DetailImportSupplierComponent
  },
  {
    path: "edit-purchase",
    component: EditComponent
  }
]

@NgModule({
  declarations: [
    PurchaseComponent,
    DetailImportSupplierComponent,
    AddComponent,
    EditComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    ReactiveFormsModule,
    FormsModule,
    TranslateModule,
    BillType2Module
  ]
})
export class PurchaseModule { }
