import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhPurchaseInvoice } from 'src/app/cafe/interface/vh-purchase-invoice';
import { LanguageService } from 'src/app/cafe/services/language.service';
@Component({
  selector: 'app-purchase',
  templateUrl: './purchase.component.html',
  styleUrls: ['./purchase.component.scss']
})
export class PurchaseComponent implements OnInit {

  public searchValue: string = ''
  public invoiceShow: any[] = []
  public invoiceToday: any[] = [];
  startTime: Date = new Date()
  endTime: Date = new Date()
  listFilter: Array<any> = [{ value: 1, name: "Latest" }, { value: 2, name: "Oldest" }, { value: 3, name: "Low to High value" }, { value: 4, name: "High to Low value" }]
  tableHeight: string;
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  constructor(
    private vhQuery: VhQuery,
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private cdRef: ChangeDetectorRef
  ) {
    this.startTime.setHours(0, 0, 0, 0)
    this.endTime.setHours(23, 59, 59, 59)
  }

  ngOnInit() {
    if (this.dataRestore) {
      this.searchValue = this.dataRestore.searchValue
      this.filterType = this.dataRestore.filterType
      
    }
    else { this.filterType = 1;  }
    if (!this.invoiceToday) this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => this.getInvoiceToday(this.filterType))
    else this.getInvoiceToday(this.filterType)
  } 

  /**
   * lọc hóa đơn theo ['bill_code', 'partner_name', 'payment_name', 'name_employee', 'status']
   * @param value 
   * @example this.searchBill('')
   */
  searchBill(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.invoiceShow = this.vhAlgorithm.searchList(val, this.invoiceToday, ['bill_code', 'partner_name', 'payment_name', 'status'])
    } else this.invoiceShow = [...this.invoiceToday]
  }

  /**
   * get danh sách hóa đơn nhập từ ncc hôm nay (billtype 2)
   * @example this.getInvoiceToday()
   */
  getInvoiceToday(filterType?) {
    this.vhQueryCafe.getBills_byFields({ date: { $gte: this.startTime, $lte: this.endTime }, bill_type: { $in: [2, 6, 22] }, id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id } }, {})
      .then((invoice: any[]) => {
        console.log('getBills_byFields', invoice);

        if (!this.invoiceToday) this.vhComponent.hideLoading(0)
        this.invoiceToday = invoice.map(item => {
          return { ...item, employee_name: this.vhAuth.getlocalEmployee(item.id_employee).name, partner_name: this.renderPartner(item.id_supplier), payment_name: this.renderPayment(item.payment_type, item.id_wallet), status: this.renderStatus(item.bill_type) }
        })
        if (filterType) this.sortFilter(filterType)
        else this.searchBill(this.searchValue)
      })
  }

  /**
   * trả về tên trạng thái của hóa đơn
   * @example let status = this.renderStatus(2)
   */
  renderStatus(bill_type: 2 | 6 | 22) {
    switch (bill_type) {
      case 6: return this.languageService.translate('Temporary')
      case 22: return this.languageService.translate('Cancelled')
      default: return this.languageService.translate('Entered')
    }
  }
  /**
   * trả về tên ncc
   * @example let supplier = this.renderPartner('DKLJWNIUDHWIUHGDIWD')
   */
  renderPartner(id_partner: string) {
    let supplier: string = this.vhQueryCafe.getlocalSupplier(id_partner).name

    return supplier ? supplier : this.languageService.translate("Not found")
  }
  /**
   * trả về tên phương thức thanh toán
   * @example let payment = this.renderPayment(1)
   */
  renderPayment(payment_type: 1 | 2 | 3, id_wallet: string = null) {
    switch (payment_type) {
      case 1: return this.languageService.translate('Cash')
      case 2: return this.languageService.translate('Debit')
      case 3: return this.vhQueryCafe.getlocalWallet(id_wallet).name
    }
  }

  goToCart() {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType }
    if (this.vhAuth.checkMyPermission("purchase_enable_create_bill")) this.router.navigate(["/cafe/dashboard/management/invoice/purchase/add"], { state: { dataRestore } })
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  goToDetail(id) {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType }
    if (this.vhAuth.checkMyPermission("purchase_enable_view_bill_today")) this.router.navigate(["/cafe/dashboard/management/invoice/purchase/detail"], { state: { id, dataRestore } })
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  public filterType: 1 | 2 | 3 | 4 = 2;
 
  
  /**
   * sort invoiceToday
   * @example this.sortFilter(1)
   */
  sortFilter(filterType: 1 | 2 | 3 | 4) {
    switch (filterType) {
      case 1: this.invoiceToday.sort((prev, next) => Date.parse(next.date) - Date.parse(prev.date));  break;
      case 2: this.invoiceToday.sort((prev, next) => Date.parse(prev.date) - Date.parse(next.date));   break;
      case 3: this.invoiceToday.sort((prev, next) => (prev.total * (1 + prev.tax / 100)) - (next.total * (1 + next.tax / 100))); break;
      case 4: this.invoiceToday.sort((prev, next) => (next.total * (1 + next.tax / 100)) - (prev.total * (1 + prev.tax / 100)));  break;
    }

    this.searchBill(this.searchValue)
  }
  onCurrentPageDataChange(event) { }
  ngAfterViewChecked() {
    if (document.querySelector("#sales-invoice-today") && document.querySelector(".ant-table-thead") && document.querySelector(".sales-invoice-today-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#sales-invoice-today").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".sales-invoice-today-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 80 + "px";
    }
    this.cdRef.detectChanges();
  }
  goBack() {
    this.router.navigate(["/cafe/dashboard/management/invoice"])
  }

  /** hàm sort cho các cột */
  dateCol = false;
  bill_codeCol = false;
  partner_nameCol = false;
  payment_nameCol = false;
  employee_nameCol = false;
  totalCol = false;
  statusCol = false;
  /** Hàm thực hiện sắp xếp theo collect
   *
   * @param colName       // tên cột muôn sắp xếp
   */
  sortTable(colName){
    switch (colName) {
      case 'date':
        if(this.dateCol){
          this.invoiceShow = this.vhAlgorithm.sortDatebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortDatebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'bill_code':
        if(this.bill_codeCol){
          this.invoiceShow = this.vhAlgorithm.sortStringbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortStringbyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'partner_name':
        if(this.partner_nameCol){
          this.invoiceShow = this.vhAlgorithm.sortStringbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortStringbyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'payment_name':
        if(this.payment_nameCol){
          this.invoiceShow = this.vhAlgorithm.sortStringbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortStringbyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'employee_name':
        if(this.employee_nameCol){
          this.invoiceShow = this.vhAlgorithm.sortStringbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortStringbyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'status':
        if(this.statusCol){
          this.invoiceShow = this.vhAlgorithm.sortStringbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortStringbyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'total':
        if(this.totalCol){
          this.invoiceShow = this.vhAlgorithm.sortNumberbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortNumberbyDESC([...this.invoiceShow], colName);
        }
        break;
    }
  }
}
